html, body {
    min-height: 100%;
}

p {
    margin: 0;
    padding: 0 0 1rem 0;
}

h1, h2 {
    margin: 0 0 .5em;
    line-height: 1.2em;
}

.outlined {
    outline: 2px solid #eee;
}


aside {
    font-size: .9rem;
    font-weight: bold;
    padding: .5rem;
    color: #059;
    word-break: break-word;
}

aside .padded {
    background: #059;
    color: white;
    padding: 1rem;
}

.App {
    max-width: 70rem;
    margin: auto;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

header {
    background: #eee;
    padding: 1rem;
}

.main-content {
    padding: 1rem;
    flex-grow: 1;
}

ul {
    padding: 0 0 0 1.5rem;
    margin: 0;
}

ul ul {
    margin-top: .5rem;
}

li {
    padding: .5rem 0;
}

footer {
    height: 25rem;
    background: #eee;
    padding: 1rem;
}